<template>
  <div v-if="isMobile" class="fixed bottom-0 left-0 z-20 w-full bg-gray-50 shadow shadow-2xl">
    <div class="flex flex-wrap items-center justify-evenly p-1 space-x-5 text-sm text-gray-500 dark:text-gray-400">
      <router-link v-for="link in links" :key="link.id" :to="link.to" class="flex items-center flex-col hover:no-underline p-2 ml-2 rounded-lg dark:text-white" aria-current="page" @click="scrollToSection(link.id)" :class="{ 'vue-menu-active-link': activeSection === link.id }">
        <icon :name="link.icon"></icon>
        <span class="flex-1 whitespace-nowrap">{{ link.label }}</span>
      </router-link>
    </div>
  </div>
</template>

<script>
import icon from './icons.vue';
export default {
  components: {icon},
  props: {
    blog: Array,
  },
  data() {
    return {
      isMobile: false,
      activeSection: 'home',
      links: [
        { id: 'home', to: { path: '/', hash: '' }, label: 'Home', icon: 'mobile-home' },
        { id: 'about', to: '/#about', label: 'About', icon: 'mobile-about' },
        { id: 'projects', to: '/#projects', label: 'Projects', icon: 'mobile-project' }
      ]
    };
  },
  mounted() {
    this.isMobile = this.checkIsMobile();
  },
  methods: {
    checkIsMobile() {
      return window.matchMedia('(max-width: 767px)').matches;
    },
    scrollToSection(id) {
      this.activeSection = id;
      if (id === 'home') {
        window.scrollTo({ top: 0, behavior: 'smooth' });
      } else {
        const el = document.getElementById(id);
        if (el) {
          const navbarHeight = document.querySelector('nav').offsetHeight;
          const actualOffset = el.offsetTop - navbarHeight;
          const offset = actualOffset - 20; 
          setTimeout(() => {
            window.scrollTo({ top: offset, behavior: 'smooth' });
          }, 4);
        }
      }
    }
  }
};
</script>

<style scoped>
.vue-menu-active-link {
  background-color: var(--primary-color);
  color: white;
}
</style>
