<script>

export default {
  data() {
    return {
      formData: {
        name: '',
        email: '',
        message: ''
      },
      formErrors: {},
      emailAddress: 'kailashinfotech4@gmail.com'
    };
  },
  methods: {
    submitForm() {
      this.validateForm();
      if (Object.keys(this.formErrors).length === 0) {
        // Form is valid, submit the form
        console.log('Form submitted:', this.formData);
        // You can add logic here to send the form data to your backend for processing (e.g., sending email)
      }
    },
    validateForm() {
      this.formErrors = {};

      // Validate name
      if (!this.formData.name) {
        this.formErrors.name = 'Name is required';
      }

      // Validate email
      if (!this.formData.email) {
        this.formErrors.email = 'Email is required';
      } else if (!this.isValidEmail(this.formData.email)) {
        this.formErrors.email = 'Invalid email address';
      }
    },
    isValidEmail(email) {
      // Basic email validation
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      return emailRegex.test(email);
    },
    openMailClient() {
      window.location.href = 'mailto:' + this.emailAddress;
    }
  }
};
